import axios from "axios";
import React, { useContext, useEffect } from "react";
import { useState } from "react";
import { useParams } from "react-router-dom";
import { cartcontext } from "../../context/cartcontext";
import toast from 'react-hot-toast';




export default function Productdetails() {

const {setnumOfCartItems} = useContext(cartcontext)

  const { id } = useParams();
  console.log(id);

  const [prodetails, setprodetails] = useState(null);
  async function getdetails() {
    const res = await axios.get(
      `https://ecommerce.routemisr.com/api/v1/products/${id}`
    );
    console.log(res.data);
    setprodetails(res.data);
  }

  useEffect(() => {
    getdetails();
    return () => {};
  }, []);


  const {addToCart} = useContext(cartcontext)

  async function add(id)
  {
    const res =  await addToCart(id);
if(res.status === "success")
{
  toast.success( res.message)

  setnumOfCartItems(res.numOfCartItems)


}

else {
  toast.error("This didn't work.")

}

  }

  return (
    <>

    {prodetails !== null ?   <div className="container">
        <div className="row g-5 ">
          <div className="col-md-4">
            <img
              src={prodetails.data.imageCover}
              alt=""
              className="img-fluid"
            />
          </div>
          <div className="col-md-8 d-flex justify-content-center flex-column align-items-center">
            <p>{prodetails.data.title}</p>
            <div className="mb-4"> {prodetails.data.description}</div>
            <div  className="mb-4" >{prodetails.data.category.name}</div>
            <div className="d-flex justify-content-between ">
              <div className="mb-4" >{prodetails.data.price} EGP </div>
              <div className="mb-4" >
              <i className="fa-solid fa-star"></i>  {prodetails.data.ratingsAverage}
               
              </div>
            </div>
            <button onClick= {()=> add(prodetails.data._id)} className="btn btn-info w-100 text-white">add to cart</button>
          </div>
        </div>
      </div> : 'loadinggggggggg'}
    
    </>
  );
}
