import toast from "react-hot-toast";
import { useFormik } from "formik";
import React from "react";
import axios from "axios";

export default function Resetpass() {
  let myresetCode = {
    resetCode: "",
  };

  let myformik = useFormik({
    initialValues: {
        resetCode: myresetCode,
    },
    onSubmit: ressetpass,
  });
  async function ressetpass(v) {
    try {
      console.log(v);
      const {data} = await axios.post(
        "https://ecommerce.routemisr.com/api/v1/auth/verifyResetCode",
        v
      );
      console.log(data.status );
      if(data.status == "Success" )
      {
          toast.success(data.status );
          setTimeout(function(){    window.location.assign('/ResetPassword');},2000);

      }
    } catch (e) {toast.error(e.response.data.message)}
  }

  return (
    <>
      <div className="container">
        <div className="row d-flex   justify-content-center align-items-center">
          <div className="col-md-6 mt-5">
            <form className="p-5  bg-light rounded shadow-sm" onSubmit={myformik.handleSubmit}>
              <div className="mt-3 row">
                <label htmlFor="resetCode" className="col-sm-2 col-form-label">
                  resetCode
                </label>
                <div className="col-sm-10">
                  <input
                    type="text"
                    onBlur={myformik.handleBlur}
                    onChange={myformik.handleChange}
                    className="form-control"
                    id="resetCode"
                    placeholder="0000"
                  />
                </div>
                <div className="col-sm-12 mt-5">
                  {" "}
                  <button
                    type="
                "
                    className="btn btn-primary"
                  >
                    {" "}
                    send
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}
