import axios from "axios";
import React, { useEffect } from "react";
import { useState } from "react";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";

export default function Prands() {
  const [brands, setbrands] = useState(null);
  const [brandsdetails, setbrandsdetails] = useState(null);
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => {
    setShow(true);
  };
  async function getallbrands() {
    const { data } = await axios.get(
      `https://ecommerce.routemisr.com/api/v1/brands`
    );
    setbrands(data.data);
    console.log(data.data);
  }
  async function getbrand(id) {
    try {
      const { data } = await axios.get(
        `https://ecommerce.routemisr.com/api/v1/brands/${id}`
      );

      setbrandsdetails(data.data);
      console.log("details", brandsdetails);
    } catch (e) {
      console.log(e);
    }
  }

  useEffect(() => {
    getallbrands();
  }, []);

  return (
    <div className="container">
      <div className="row g-4 text-center">
        <h4>All brands</h4>

        {brands != null
          ? brands.map(function (brands, indx) {
              return (
                <div className="col-md-3 text-center " key={indx}>
                  <div className="border" onClick={handleShow}>
                    <img
                      src={brands.image}
                      alt=""
                      onClick={() => {
                        getbrand(brands._id);
                      }}
                      className="w-100"
                    />
                    <div className="py-3 border-top "> {brands.name} </div>
                  </div>
                </div>
              );
            })
          : ""}

        <Modal show={show} onHide={handleClose}>
          <Modal.Header closeButton>
            <Modal.Title className="w-100">
             
              {brandsdetails != null ? <h5 className="text-center text-primary ">{brandsdetails.name}</h5> : ""}
            </Modal.Title>
          </Modal.Header>
          <Modal.Body className="text-center">
            {brandsdetails != null ? (
              <img src={brandsdetails.image} alt="" className="w-50 text-center" />
            ) : (
              ""
            )}
          </Modal.Body>
          <Modal.Footer>
            <Button className= "btn btn-primary w-100"  onClick={handleClose}>
              Close
            </Button>
          
          </Modal.Footer>
        </Modal>
      </div>
    </div>
  );
}
