import axios from "axios";
import { Children, useState } from "react";
import { createContext } from "react";

export const whislistcontext = createContext();

export function Whislistcontextprovider(props) {
  const [checked, setchecked] = useState('');
 
  const [wishlist, setwishlist] = useState(null);

const {id} ="6428bf40dc1175abc65c9ff1";
  async function addtowishlist(id) {
    try {
      const { data } = await axios.post(
        `https://ecommerce.routemisr.com/api/v1/wishlist`,
        {
          productId: id,
        },
        {
          headers: {
            token: localStorage.getItem("tkn"),
          },
        }
      );
      setchecked(true);
      return data;
    } catch (error) {
      console.log(error);
    }
  }

  async function getwishlist() {
    try {
      const { data } = await axios.get(
        `https://ecommerce.routemisr.com/api/v1/wishlist`,
        {
          headers: {
            token: localStorage.getItem("tkn"),
          }
        }
      );
      console.log(data.data)
      setwishlist(data.data);
       
    } catch (error) {
      console.log(error);
    }
  }
  async function deleteitem(id) {
    try {
      const { data } = await axios.delete(
        `https://ecommerce.routemisr.com/api/v1/wishlist/${id}`,
        {
          headers: {
            token: localStorage.getItem("tkn"),
          }

        }
      ); 
    
      getwishlist()
      
    } catch (error) {
      console.log(error);
    }
  }
  return (
    <>
      <whislistcontext.Provider value={{deleteitem, addtowishlist,getwishlist, checked, setchecked ,wishlist, setwishlist}}>
        {props.children}
      </whislistcontext.Provider>
    </>
  );
}
