import React from "react";
import { useContext } from "react";
import { Link } from "react-router-dom";
import { mytoken } from "../../context/Tokencontext";
import { cartcontext } from "../../context/cartcontext";

export default function Navbar() {
  const { token } = useContext(mytoken);
  const { numOfCartItems } = useContext(cartcontext);
  console.log(token)
  return (
    <>
      <div className="top_nav">
        <div className="container ">
          <nav className="navbar navbar-expand-lg bg-light mb-5">
            <div className="container-fluid">
              <Link className="navbar-brand" to="Home">
                START FRAMEWORK
              </Link>
              <button
                className="navbar-toggler"
                type="button"
                data-bs-toggle="collapse"
                data-bs-target="#navbarTogglerDemo02"
                aria-controls="navbarTogglerDemo02"
                aria-expanded="false"
                aria-label="Toggle navigation"
              >
                <span className="navbar-toggler-icon"></span>
              </button>
              <div className="collapse navbar-collapse " id="navbarNav">
                <ul className="navbar-nav me-auto ">


                  {token ? <>
                  
                     <li className="nav-item">
                    <Link className="nav-link" to="Home">
                    home
                    </Link>
                  </li>
                
                  <li className="nav-item">
                
                    <Link className="nav-link" to="/Prands">
                      Prands
                    </Link>
                  </li>
                  <li className="nav-item">
                 
                    <Link className="nav-link" to="Listwish">
                      wishlist
                    </Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link" to="Category">
                      Category
                    </Link>
                  </li>
                  </> : ''}
               
                </ul>

                <ul className="nav ms-auto  ">
                  <li className="nav-item">
                 
                    {/* <Link className="nav-link" to="Profile">
                      Profile
                    </Link> */}
                  </li>
                
                  {token ? '': <>
                    <li className="nav-item">
                  <Link className="nav-link" to="Sigenup">
                      Sigenup
                    </Link>
                  </li>
                  <li className="nav-item">
                 
                    <Link className="nav-link" to="Login">
                      Login
                    </Link>
                  </li>
                  </> }
                    
            
                  {token ? <>
                        <li className="nav-item">
                 <span className="nav-link">logout </span>
                  </li>
 <li className="nav-item">
                    <Link className="nav-link" to="Cart">
       {numOfCartItems === 0 ? <i className="fa-brands fa-opencart"></i> : <><i className="fa-brands fa-opencart"></i> {numOfCartItems} </>   }
                    </Link>
                  </li>

               </>:''
                                    }
                       
                </ul>
              </div>
            </div>
          </nav>
        </div>
      </div>
    </>
  );
}
