import { RouterProvider, createBrowserRouter, createHashRouter } from "react-router-dom";
import "./App.css";
import Sigenup from "./component/Sigenup/Sigenup.jsx";
import Prands from "./component/Prands/Prands";
import Proudects from "./component/Proudects/Proudects";
// import Cartt from "./component/Cart/Cartt";
import Login from "./component/Login/Login";
import Notfound from "./component/Notfound/Notfound";
import Home from "./component/Home/Home";
import Category from "./component/Category/Category";
import Layout from "./component/Layout/Layout";
import Productdetails from "./component/Productdetails/Productdetails";
import Funccointprovider from "./context/Counter";
import Profile from "./component/Profile/Profile";
import { Tokencontextprovider } from "./context/Tokencontext";
import Cartcontextprovider from "./context/cartcontext";
import { Toaster } from "react-hot-toast";
import { Whislistcontextprovider } from "./context/wishlist";
import Listwish from "./component/Listwish/Listwish";
import Catdetails from "./component/Catdetails/Catdetails";
import { QueryClient, QueryClientProvider } from "react-query";
import Checkout from "./component/Checkout/Checkout";
import Cart from "./component/Cart/Cart";
import Allorders from "./component/Allorders/Allorders";
import Forgetpass from "./component/Forgetpass/Forgetpass";
import Resetpass from "./component/Resetpass/Resetpass";
import ResetPassword from "./component/ResetPassword/ResetPassword";

const queryclient = new QueryClient();

function App() {
  const router = createHashRouter([
    {
      path: "",
      element: <Layout />,
      children: [
        { path: "/", element: <Home /> },
        { path: "/home", element: <Home /> },
        { path: "/Prands", element: <Prands /> },

        { path: "Proudects", element: <Proudects /> },
        { path: "Category", element: <Category /> },
        { path: "/Cart", element: <Cart /> },

        { path: "/listwish", element: <Listwish /> },

        { path: "/Sigenup", element: <Sigenup /> },
        { path: "/Login", element: <Login /> },

        { path: "/Checkout", element: <Checkout /> },
        { path: "/Resetpass", element: <Resetpass /> },


        { path: "/Profile", element: <Profile /> },
        { path: "/Catdetails/:id", element: <Catdetails /> },
        { path: "/Allorders", element: <Allorders /> },
        { path: "/Forgetpass", element: <Forgetpass/> },
        
        { path: "/ResetPassword", element: <ResetPassword/> },

        { path: "/Productdetails/:id", element: <Productdetails /> },
        { path: "*", element: <Notfound /> },
  
      ],
    },
  ]);
  return (
    <>
      {/* <Api/> */}
      <QueryClientProvider client={queryclient}>
        <Whislistcontextprovider>
          <Cartcontextprovider>
            <Tokencontextprovider>
              <Funccointprovider>
                <Toaster />
                <RouterProvider router={router} />
              </Funccointprovider>
            </Tokencontextprovider>
          </Cartcontextprovider>
        </Whislistcontextprovider>
      </QueryClientProvider>
    </>
  );
}

export default App;
