import axios from "axios";
import toast from "react-hot-toast";

const { createContext, useState } = require("react");

export const cartcontext = createContext();

export default function Cartcontextprovider(props) {
  const [numOfCartItems, setnumOfCartItems] = useState(null);
  const [totalCartPrice, settotalCartPrice] = useState(null);
  const [cart, setcart] = useState(null);

  async function addToCart(Id) {
    const res = await axios.post(
      `https://ecommerce.routemisr.com/api/v1/cart`,
      {
        productId: Id,
      },

      {
        headers: { token: localStorage.getItem("tkn") },
      }
    );

    setnumOfCartItems(res.data.numOfCartItems);
    getusercart();
    return res.data;
  }

  function updatecount(id, count) {
    try {
      const res = axios.put(
        `https://ecommerce.routemisr.com/api/v1/cart/${id}`,
        {
          count: count,
        },
        {
          headers: { token: localStorage.getItem("tkn") },
        }
      );

      return res;
    } catch (e) {
      console.log(e);
    }
  }

  async function getusercart() {
    try {
      const res = await axios.get(
        "https://ecommerce.routemisr.com/api/v1/cart",
        {
          headers: { token: localStorage.getItem("tkn") },
        }
      );

      // setnumOfCartItems(res.data.numOfCartItems);
      console.log("num ", res.data.numOfCartItems);
      setnumOfCartItems(res.data.numOfCartItems);
      return res;
    } catch (error) {
      console.log("no dataaaa in cart");
    }
  }

  async function getnumcartitem() {
    const res = await axios.get("https://ecommerce.routemisr.com/api/v1/cart", {
      headers: { token: localStorage.getItem("tkn") },
    });

    return setnumOfCartItems(res.data.numOfCartItems);
  }

  async function deleteproduct(id) {
    const res = await axios.delete(
      `https://ecommerce.routemisr.com/api/v1/cart/${id}`,
      {
        headers: { token: localStorage.getItem("tkn") },
      }
    );

    return setnumOfCartItems(res.data.numOfCartItems);
  }

  async function clearCart() {
    const res = await axios.delete(
      "https://ecommerce.routemisr.com/api/v1/cart",
      {
        headers: { token: localStorage.getItem("tkn") },
      }
    );
    console.log("delte ", res.data);
  }
  async function checkoutfun(id, shippingAddress) {
 
    try{
      const res = await axios.post(
        `https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${id}`,
        {
          shippingAddress: shippingAddress
        },
  
        {
          headers: { token: localStorage.getItem("tkn") }
        }
      );
 
      return res.data.session.url
    }
    catch(e)
    {
      console.log(e)
    }
  }
  return (
    <cartcontext.Provider
      value={{
        addToCart,
        getusercart,
        setnumOfCartItems,
        numOfCartItems,
        getnumcartitem,
        deleteproduct,
        clearCart,
        updatecount,
        checkoutfun
      }}
    >
      {props.children}
    </cartcontext.Provider>
  );
}
