import React from "react";
import axios from "axios";
import { useFormik } from "formik";
import { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useContext } from "react";
import { mytoken } from "../../context/Tokencontext";

export default function Login() {
  const { settoken , token } = useContext(mytoken);

  let user = {
    email: " ",
    password: "",
  };

  let navigate = useNavigate();
  const [erorr, seterorr] = useState(null);
  async function login(X) {
    try {
      let reponse = await axios.post(
        `https://ecommerce.routemisr.com/api/v1/auth/signin`,
        X
      );
      console.log(reponse.data.message);
      console.log(reponse.data.token);
      settoken(reponse.data.token);
     localStorage.setItem('tkn', reponse.data.token)

       navigate('/Profile')
    } catch (myerr) {
      console.log(myerr.response.data.message);
      seterorr(myerr.response.data.message);
    }
    // let reponse = await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/signup`, X)
    //   .catch( function (myerr)
    //   { console.log("erorr")
    //     console.log(myerr.response.data.message)
    //    }
    //    );
    //    console.log(reponse.data.message)
  }
  const myformik = useFormik({
    initialValues: user,
    onSubmit: login,
  });

  return (
    <>
      <div className="container">
        <div className="row justify-content-center">
          <div className="col-md-8">
            <form  className="p-5  bg-light rounded shadow-sm" onSubmit={myformik.handleSubmit}>
              {erorr !== null ? <p className="bg-danger">{erorr} </p> : ""}
              <div className="mb-3 row">
                <label htmlFor="email" className="col-sm-2 col-form-label"> email</label>
                <div  className="col-sm-10">
                       <input
                  onChange={myformik.handleChange}
                  value={myformik.values.email}
                  type="email"
                  className="form-control"
                  id="email"
                  placeholder="Enter email"
                />           
              </div>  
              </div>

              <div className="mb-3 row">
                <label htmlFor="password" className="col-sm-2 col-form-label"> password</label>
                <div  className="col-sm-10">
                <input
                  onChange={myformik.handleChange}
                  value={myformik.values.password}
                  type="password"
                  className="form-control"
                  id="password"
                  placeholder="Enter phone"
                />
                </div>
              </div>
<div  className="text-centert col-12 ">
     <button type="submit" className="btn btn-primary">
                Submit
              </button>
</div>
           
            </form>
            <Link to="/Sigenup" className="text-info text-decoration-none">
              {" "}
              <span className="text-info text-decoration-none">
                go to sign up
              </span>
            </Link>


            <Link to="/Forgetpass" className="text-info text-decoration-none">
              {" "}
              <span className="text-info text-decoration-none">
                forget password
              </span>
            </Link>
          </div>
        </div>
      </div>
    </>
  );
}
