import React, { useContext, useEffect, useState } from "react";
import { cartcontext } from "../../context/cartcontext";
import { CirclesWithBar } from "react-loader-spinner";
import toast from "react-hot-toast";
import Checkout from "../Checkout/Checkout";
import { Link } from "react-router-dom";

export default function Cart() {
  const {
    getusercart,
    deleteproduct,
    clearCart,
    updatecount,
    totalCartPrice,
    setnumOfCartItems,
    numOfCartItems,
  } = useContext(cartcontext);

  const [cartdataa, setcartdata] = useState(null);
  const [count, setcount] = useState(null);

  async function getcartdata() {
    try {
      const resp = await getusercart();
      setcartdata(resp);
    } catch (e) {
      console.log("no data in cart this userrrrrrr", e);
    }
  }

  async function deleteall() {
    try {
      await clearCart();
      setcartdata(null);
      //  totalCartPrice(0)
      //  count(0)
      return getcartdata();
    } catch (e) {
      console.log("delete eror", e);
    }
  }
  async function prodelete(id) {
    await deleteproduct(id);
    return getcartdata();
  }

  async function updatecartcount(id, count) {
    const { data } = await updatecount(id, count);
    getcartdata();
    console.log(data.status);
    setnumOfCartItems(numOfCartItems + 1);

    if (data.status == "success") {
      toast.success(data.status);
    } else {
      toast.error(data.status);
    }
  }

  useEffect(() => {
    getcartdata();
  }, []);

  return (
    <>
      <div className="container">
        <div className="row">
          <div className="col-md-12 d-flex justify-content-between">
            shop cart
            <button
              onClick={() => {
                deleteall();
              }}
              type=""
              className="btn btn-outline-danger  mx-2"
            >
              clear cart
            </button>
          </div>
          <span className="text-primary mb-5">
            {cartdataa !== null
              ? "total price : " + cartdataa.data.data.totalCartPrice + "EGP"
              : ""}
          </span>
        </div>
        {cartdataa == null ? (
          <div id="msg" className="d-flex justify-content-center">
            <CirclesWithBar
              height="100"
              width="100"
              color="#1d6df7"
              wrapperStyle={{}}
              wrapper
              className=""
              visible={true}
              outerCircleColor=""
              innerCircleColor=""
              barColor=""
              ariaLabel="circles-with-bar-loading"
            />
          </div>
        ) : (
          cartdataa.data.data.products.map(function (product, indx) {
            return (
              <div className="row my-1" key={indx}>
                <div className="col-md-1 ">
                  <img
                    src={product.product.imageCover}
                    className="w-100"
                    alt=""
                  />
                </div>
                <div className="col-md-9">
                  <div>{product.product.title}</div>
                  <div className="text-primary">price : {product.price}</div>

                  <div className="text-danger">
                    <button
                      onClick={() => {
                        prodelete(product.product._id);
                      }}
                      className="btn btn-sm mt-2 btn-outline-danger"
                    >
                      <i className="fa-solid fa-trash fa-2xs text-danger"></i>{" "}
                      remove
                    </button>
                  </div>
                </div>

                <div className="col-md-2 text-right">
                  <button
                    type=""
                    onClick={() => {
                      updatecartcount(product.product._id, product.count + 1);
                    }}
                    className="btn btn-outline-primary mx-2 py-1"
                  >
                    +
                  </button>
                  {product.count}
                  <button
                    type=""
                    onClick={() => {
                      updatecartcount(product.product._id, product.count - 1);
                    }}
                    className="btn btn-outline-primary mx-2 py-1"
                  >
                    -
                  </button>
                </div>
              </div>
            );
          })
        )}
        {numOfCartItems != 0 ? (
          <Link to="/Checkout">
            <div className="col-md-12 text-center mt-4">
              <button type="" className="btn btn-primary w-25">
                pay now
              </button>
            </div>
          </Link>
        ) : (
          ""
        )}
      </div>
    </>
  );
}
