import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import toast from "react-hot-toast";
import { Link } from "react-router-dom";
import Catdetails from "../Catdetails/Catdetails";

export default function Category() {

async function getallcat()
{
  try{
     const {data}= await axios.get(`https://ecommerce.routemisr.com/api/v1/categories`);
     setcategory(data.data)
     console.log(data.data)
  }
  catch(e)
  {
  toast.error(e.response.data.message)
  }


}
const [allcategory, setcategory] = useState(null)
const imgheight={height: 350   }
useEffect(() => {
  getallcat()

  return () => {
    
  }
}, [])

  return <>
  <div className="container">
    <div className="row g-4 justify-content-center">
      
    { allcategory != null ? <>
        {allcategory.map(function(category,indx){
          return  <div className="col-md-4" key={indx}>
            <Link to= {`/Catdetails/${category._id}`}>
          
          <div className="border shadow "> 
            <img src={category.image} alt="" style={imgheight} className="w-100 myimg"/>
          <div className=" text-center my-4 text-primary fw-bolder">
           {category.name}
          </div>
          </div>
            </Link>
        </div>
        })}
        </> :
         ''
         }
      

   
    </div>
  </div>
  
  </>;
}
