
import React from 'react'
import axios from 'axios'
import { useFormik } from 'formik'
import { useState } from 'react'

export default function Sigenup() {

  let user = {
    name: " ",
    email: " ",
    password: "",
    rePassword: "",
    phone: " "
  }
const [erorr, seterorr] = useState(null)
  async function regesternewuser(X) {
try{
  let reponse = await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/signup`, X);
  console.log(reponse.data.message)
}
catch(myerr)
{
  console.log(myerr.response.data.message)
  seterorr(myerr.response.data.message)
}
    // let reponse = await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/signup`, X)
    //   .catch( function (myerr) 
    //   { console.log("erorr")
    //     console.log(myerr.response.data.message)
    //    }
    //    );
    //    console.log(reponse.data.message)
  }
  const myformik = useFormik({
    initialValues: user,
    onSubmit: regesternewuser

  })

  return (
    <>
      <div className="container">
        <div className="row justify-content-center">
          <div className="col-md-8">
            <form onSubmit={myformik.handleSubmit}>
            {erorr !== null ? <p className='bg-danger'>{erorr} </p> : '' }  
              <div className="Form-group mb-3">
                <label htmlFor="name"> name</label>
                <input onChange={myformik.handleChange} value={myformik.values.name} type="text" className="form-control" id="name" placeholder="Enter name" />
              </div>
              <div className="Form-group mb-3">
                <label htmlFor="email"> email</label>
                <input onChange={myformik.handleChange} value={myformik.values.email} type="email" className="form-control" id="email" placeholder="Enter email" />
              </div>

              <div className="Form-group mb-3">
                <label htmlFor="emphoneail"> phone</label>
                <input onChange={myformik.handleChange} value={myformik.values.phone} type="tel" className="form-control" id="phone" placeholder="Enter phone" />
              </div>


              <div className="Form-group mb-3">
                <label htmlFor="password"> password</label>
                <input onChange={myformik.handleChange} value={myformik.values.password} type="password" className="form-control" id="password" placeholder="Enter phone" />
              </div>

              <div className="Form-group mb-3">
                <label htmlFor="rePassword"> repassword</label>
                <input onChange={myformik.handleChange} value={myformik.values.rePassword} type="password" className="form-control" id="rePassword" placeholder="Enter phone" />
              </div>



              <button type="submit" className="btn btn-primary">Submit</button>
            </form>
          </div>
        </div>
      </div>
    </>
  )
}
