import axios from "axios";
import React, { useContext, useState } from "react";
import { useEffect } from "react";
import { Link } from "react-router-dom";
import { cartcontext } from "../../context/cartcontext";
import toast, { Toaster } from "react-hot-toast";
import { TailSpin } from "react-loader-spinner";
import { whislistcontext } from "../../context/wishlist";
import Slider from "react-slick";


export default function Home() {
  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow:5,
    slidesToScroll: 1
  };

  const { addToCart, setnumOfCartItems, numOfCartItems } =
    useContext(cartcontext);
  const { addtowishlist, checked } = useContext(whislistcontext);
  //  setIcon('fa-solid')
  async function addtomyfav(id) {
    const res = await addtowishlist(id);

    if (res.status === "success") {
      toast.success(res.message);
    } else {
      console.log("eroorrr");
    }
  }

  async function getproducts() {
    const res = await axios.get(
      `https://ecommerce.routemisr.com/api/v1/products`
    );
    // console.log("h", res.data.data);
    setnumOfCartItems(numOfCartItems);
    setproducts(res.data.data);
  }

  const [products, setproducts, setchecked] = useState(null);

  async function add(id) {
    const res = await addToCart(id);

    if (res.status === "success") {
      toast.success(res.message);
      setnumOfCartItems(res.numOfCartItems);
    } else {
      toast.error(res.message);
    }
  }
  async function getallcat()
{
  try{
     const {data}= await axios.get(`https://ecommerce.routemisr.com/api/v1/categories`);
     setcategory(data.data)
     console.log("sss",data.data)
  }
  catch(e)
  {
  toast.error(e.response.data.message)
  }


}const [allcategory, setcategory] = useState([])

  useEffect(() => {
    getallcat(); 
     getproducts();
  
    return () => {};
  }, []);


  return (
    <>
    
      <div className="container f">
        <div className="row g-5">
        <div className="col-md-12">
        <Slider {...settings}>

{allcategory != null? allcategory.map((x,indx)=>{return<div key={indx}><img src={x.image} height={200} className="w-100"/>  <h5 className="text-center my-3">{x.name}</h5></div>  }):''}

   </Slider>
    {/* <Slider {...settings}>
      {allcategory?.images.map((img)=>{<img src={require(`../../img/port2.png`)}/>} )}
    </Slider>  */}
      </div>
          {products !== null ? (
            products.map(function (product, idx) {
              console.log("pro", product);
              return (
                <div className="col-md-2" key={idx}>
                  <i
                    onClick={() => {
                      addtomyfav(product.id);
                    }}
                    className={
                      checked ? "fa-regular fa-heart" : "fa-solid fa-heart"
                    }
                  ></i>

                  <Link to={`/Productdetails/${product.id}`}>
                    <div className="text-center">
                      <img
                        src={product.imageCover}
                        className="img-fluid mb-2"
                        alt=""
                      />
                      <h6 className="text-primary">{product.brand.name}</h6>
                      <h5 className="fs-6">
                        {product.title.split(" ").slice(0, 2).join(" ")}
                      </h5>
                      <div className="d-flex justify-content-between ">
                        <div>{product.price}EGP </div>
                        <div>
                          <i className="fa-solid fa-star fa-2xs text-warning"></i>
                          {product.ratingsAverage}
                        </div>
                      </div>
                    </div>
                  </Link>
                  <button
                    className="btn btn-primary py-1   w-100 mt-3"
                    onClick={() => add(product.id)}
                  >
                    Add
                  </button>
                </div>
              );
            })
          ) : (
            <div className="vh-75 d-flex align-items-center justify-content-center ">
              <TailSpin
                height="80"
                width="80"
                color="#0d6efd"
                ariaLabel="tail-spin-loading"
                radius="1"
                wrapperStyle={{}}
                wrapperclassName=""
                visible={true}
                className="vh-100"
              />
            </div>
          )}
        </div>
      </div>
    </>
  );
}
