import { useFormik } from "formik";
import React from "react";
import { cartcontext } from "../../context/cartcontext";
import { useContext } from "react";
import { redirect } from "react-router-dom";

export default function Checkout() {
  const { checkoutfun, getusercart } = useContext(cartcontext);
  let cart = {
    details: "",
    phone: "",
    city: "",
  };
  let myformik = useFormik({
    initialValues: cart,
    onSubmit: onlinepayment,
  });
  async function onlinepayment(values) {
    try {
      let { data } = await getusercart();
      console.log(values);
      let id = data.data._id;

      let resp = await checkoutfun(id, values);

      console.log("check res" + resp);
      window.location.assign(resp);
    } catch (e) {
      console.log(e);
    }
  }

  return (
    <div className="container">
      <div className="row">
        <div className="col-md-12">
          <form className="" onSubmit={myformik.handleSubmit}>
            <label className="form-label">details</label>
            <input
              type="text"
              id="details"
              onChange={myformik.handleChange}
              onBlur={myformik.handleBlur}
              className="form-control"
            />
            <label className="form-label">phone</label>
            <input
              type="number"
              id="phone"
              name="phone"
              onChange={myformik.handleChange}
              onBlur={myformik.handleBlur}
              className="form-control"
            />
            <label className="form-label">city</label>
            <input
              onChange={myformik.handleChange}
              onBlur={myformik.handleBlur}
              type="text"
              id="city"
              name="city"
              className="form-control"
            />
            <button type="" className="btn btn-outline-primary mt-4">
              pay
            </button>
          </form>
        </div>
      </div>
    </div>
  );
}
