import React, { useEffect } from "react";
import { whislistcontext } from "../../context/wishlist";
import { useContext } from "react";
import { cartcontext } from "../../context/cartcontext";

export default function Listwish() {
  const { getwishlist, wishlist, deleteitem } = useContext(whislistcontext);
  const { addToCart } = useContext(cartcontext);
  useEffect(() => {
    getwishlist();
  }, []);

  return (
    <div className="container ">
      <div className="row m-0  bg-light g-4">
        {wishlist != null
          ? wishlist.map(function (product, indxx) {
              return (
                
                  <div className="row" key={indxx}>
                    <div className="col-md-2 border-bottom py-3 text-center">
                      <img
                        src={product.imageCover}
                        alt=""
                        className="w-75 shadow-sm rounded border"
                      />
                    </div>
                    <div className="col-md-10 border-bottom py-3">
                      <div className="d-flex align-items-center   justify-content-between h-100">
                        <div className="d-flex flex-column  justify-content-center h-100">
                          <h6> {product.title}</h6>
                          <p className="text-primary fw-bold">
                            {product.price} EGP
                          </p>
                          <span
                            className="text-danger"
                            onClick={() => deleteitem(product._id)}
                          >
                            <i className="fa-regular fa-trash-can"></i> remove
                          </span>
                        </div>
                        <div className="">
                          <button
                            type=""
                            onClick={() => {
                              addToCart(product._id);
                            }}
                            className="btn btn-outline-primary  mx-4"
                          >
                            Add to cart
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
              
              );
            })
          : ""}
      </div>
    </div>
  );
}
