import axios from "axios";
import { useFormik } from "formik";
import React from "react";
import toast from "react-hot-toast";

export default function Forgetpass() {


    let myemail = {
        email: ""
      };


  let myformik = useFormik({
    initialValues: {
      email:myemail,
    },
    onSubmit: forgetpassword,
  });
  async function forgetpassword(v) {   
    try{
        console.log(v);
    const res = await axios.post(
      "https://ecommerce.routemisr.com/api/v1/auth/forgotPasswords",
        
   v
 
    
    );

    if(res.data.statusMsg === "success" )
    {
        toast.success(res.data.message);
        setTimeout(function(){    window.location.assign('/Resetpass');},3000);
    


    }
 
    }
    catch(e){  toast.error(e.response.data.message);}
 
  }

  return (
    <>
      <div className="container">
        <div className="row d-flex   justify-content-center align-items-center">
          <div className="col-md-6 mt-5 ">
            <form className="p-5  bg-light rounded shadow-sm" onSubmit={myformik.handleSubmit}>
              <div className="mt-3 row">
                <label
                  htmlFor="staticEmail"
                  className="col-sm-2 col-form-label"
                >
                  Email
                </label>
                <div className="col-sm-10">
                  <input
                    type="email"
                    onBlur={myformik.handleBlur}
                    onChange={myformik.handleChange}
                    className="form-control"
                    id="email"
                    placeholder="email@example.com"
                  />
                </div>
                <div className="col-sm-12 mt-5 text-center">    <button type="
                " className="btn btn-primary"> send</button></div>
            
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}
